'use client'
import { useRef, useMemo, useEffect } from 'react'
import * as THREE from 'three'

export function Particles() {
  const particles = useRef<THREE.InstancedMesh>(null!)
  const tempGeo = useMemo(() => new THREE.IcosahedronGeometry(80, 2), [])
  const dummy = useMemo(() => new THREE.Object3D(), [])

  useEffect(() => {
    const vertices = tempGeo.attributes.position.array
    for (let i = 0; i < vertices.length / 3; i++) {
      dummy.position.set(vertices[i*3], vertices[i*3+1], vertices[i*3+2])
      dummy.updateMatrix()
      particles.current.setMatrixAt(i, dummy.matrix)
    }
    particles.current.instanceMatrix.needsUpdate = true
  }, [tempGeo, dummy])

  return (
    <instancedMesh
      ref={particles}
      args={[undefined, undefined, tempGeo.attributes.position.count / 3]}
    >
      <sphereGeometry args={[0.6, 8, 8]} />
      <meshBasicMaterial 
        color={0xffff00} 
        transparent 
        opacity={0.9} 
        blending={THREE.AdditiveBlending} 
      />
    </instancedMesh>
  )
}